import React, { Component } from 'react'

export default class index extends Component {
  constructor(props) {
    super(props);

    this.state = {
      rooms: []
    }
  }
  componentDidMount() {
    const data = [{
      "presenter_id": 1,
      "video_room": "hello",
      "products": [
        {
          "product_id": 1,
          "price": 12,
          "currency": "$",
          "description": "great product1",
          "name": "my product",
          "cta1": "http://buynow_url/",
          "cta2": "http://addtocart_url",
          "image": "http://image_url"
        },
        {
          "product_id": 2,
          "price": 111,
          "currency ": "EUR",
          "description": "great product2",
          "name": "my bag",
          "cta1": "http://buynow_url/",
          "cta2": "http://addtocart_url",
          "image": "http://image_url"
        },
        {
          "product_id": 3,
          "price": 10,
          "currency ": "GBP",
          "description": "great product3",
          "name": "my phone",
          "cta1": "http://buynow_url/",
          "cta2": "http://addtocart_url",
          "image": "http://image_url"
        }
      ]
    }];

    // Set product
    this.setState({ rooms: data });
  }

  joinPlatform = (platformID) => {
    const callFrame = window.DailyIframe.createFrame();
    callFrame.join({ url: `https://fevotechnology.daily.co/${platformID}` })
  }

  render() {
    return (
      <div>
        {
          this.state.rooms.map(room => {
            return (
              <div>
              <h4 className="pl-20 text-2xl font-bold">Presenter ID: {room.presenter_id}</h4>
              <p className="pl-20 text-md">Room Link: {room.video_room}</p>
              <p className="pl-20 text-green-400 text-sm">CLICK ON THE PRODUCT TO HAVE A VIDEO/CHAT WITH THE SELLER</p>
              <div className="flex justify-around pt-16">
                {
                  room.products.map(product => {
                    return (
                      <div className="card" onClick={() => this.joinPlatform(room.video_room)}>
                        <span className="card-img">
                          <img src={product.image} alt="img" />
                        </span>
                        <p>{product.name}</p>
                        <h5>$ {product.price}</h5>
                      </div>
                    )
                  })
                }
              </div>
              </div>
            )
          })
        }
      </div>
    )
  }
}
